.container{
  display: flex;
  justify-content: center;
  
}

#drum-machine{
  margin:30px 0 0 0;
  margin:2px solid red;
  min-width: 300px;
  width: fit-content;
  height: auto;
  border:2px solid rgb(245, 170, 8);
  border-radius:10px;
  /* padding:0px 10px 10px 10px; */
}

.btn-danger{
  margin: 5px;
  width:70px;
 
  height: 50px;
}

#display{
  background-color: black;
  width: 100%;
  height: 50px;
  color:lightgreen;
  font-size: 1.5rem;
  display: flex;
  justify-content: center;
  border-radius:9px 9px 0px 0px;
  text-transform: capitalize;
}

#drum-pads{
  display: grid;
  grid-template-columns:auto auto auto;
  grid-gap: 0;
  margin:0px 0px 10px 10px;
}